import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import './App.css';

import TablesLayoutPlacement from './components/pages/TablesLayoutPlacement'
import MainPageLayoutSpecs from './components/pages/MainPageLayoutSpecs'
import MainPageLayoutSampleContent from './components/pages/MainPageLayoutSampleContent'
import MainPageComponentTables from './components/pages/MainPageComponentTables'
import LiveEnvironment from './components/pages/LiveEnvironment'
import CodeSampleWindowLayout from './components/pages/CodeSampleWindowLayout'
import LeftNavigationSpecs from './components/pages/LeftNavigationSpecs'
import Sidebar from './components/common/Sidebar'
import CustomizedPage from './components/pages/CustomizedPage';
import LeftNavMenuInteraction from './components/pages/LeftNavMenuInteraction';
import PageStructure from './components/pages/PageStructure';
import LandingPage from './components/pages/LandingPage';

const routes = [
  { path: '/',
    exact: true,
    title: 'Integration Portal - Landing Page (Web)',
    main: LandingPage
  },
  { path: '/page-structure',
    title: 'Page Structure - General Overview',
    main: PageStructure
  },
  { path: '/left-nav-menu-interaction',
    exact: true,
    title: 'Left Nav: Menu Interaction',
    main: LeftNavMenuInteraction
  },
  { path: '/left-nav-specs',
    title: 'Left Navigation Specs',
    main: LeftNavigationSpecs
  },
  { path: '/main-page-layout-specs',
    exact: true,
    title: 'Main Page Layout - Specs',
    main: MainPageLayoutSpecs
  },
  { path: '/main-page-layout-sample-content',
    title: 'Main Page Layout - Sample Content',
    main: MainPageLayoutSampleContent
  },
  { path: '/main-page-component-tables',
    title: 'Main Page Component - Tables',
    main: MainPageComponentTables
  },
  { path: '/tables-layout-placement',
    title: 'Tables - Layout Placement',
    main: TablesLayoutPlacement
  },
  { path: '/code-sample-window-layout',
    title: 'Code Sample Window - Layout',
    main: CodeSampleWindowLayout
  },
  { path: '/live-environment',
    exact: true,
    title: 'Try it now (feature) - Live Environment',
    main: LiveEnvironment
  },
  { path: '/customized-page',
    title: 'Customized Page - Based on User Preferences',
    main: CustomizedPage
  },
]

const menuLinksData = routes.map((x, i) => {
  if(i === 0 ){
    x['isSelected'] = true;
  } else {  x['isSelected'] = false }
 return { path: x.path, isSelected: x['isSelected'], title: x.title}
})

class App extends Component {
  state = {
    index: 0,
    pageName: '',
    menuLinksData,
  }

  handleSelectedPage = (index, pageName)=>{
    const {menuLinksData} = this.state;
    console.log('menulinkdsdata', menuLinksData)
    menuLinksData.map((x,i) => { 
          if(i === index){
            x.isSelected = true;
          } else {
            x.isSelected = false;
          }
    })
    // console.log('newmenu',newMenuData)
    // this.setState({index, pageName, menuLinksData})
  }

  render() {
    const { menuLinksData } = this.state;
    
    return (
      <Router>
        <div className="box">
          <Sidebar 
            menuLinksData={menuLinksData}
            currentIndex={this.state.index}
            getCurrentPageIndex={(i, title)=>this.handleSelectedPage(i, title)}
             />
             {/* getCurrentPageIndex={(i, title)=>this.handleSelectedPage(i, title)} */}
          <main>
            <div style={{ height: '100%' }}>
              {/* <Route exact path='/' component={LandingPage} />
              <Route exact path='/left-nav-specs' component={LeftNavigationSpecs} />
              <Route exact path='/tables-layout-placement' component={TablesLayoutPlacement} />
              <Route exact path='/main-page-layout-specs' component={MainPageLayoutSpecs} />
              <Route exact path='/main-page-layout-sample-content' component={MainPageLayoutSampleContent} />
              <Route exact path='/main-page-component-tables' component={MainPageComponentTables} />
              <Route exact path='/live-environment' component={LiveEnvironment} />
              <Route exact path='/code-sample-window-layout' component={CodeSampleWindowLayout} />
              <Route exact path='/customized-page' component={CustomizedPage} />
              <Route exact path='/left-nav-menu-interaction' component={LeftNavMenuInteraction} />
              <Route exact path='/page-structure' component={PageStructure} /> */}
              
              {routes.map((route, index) => {
                const Component = route.main;
                return(
                  <Route
                    key={index}
                    path={route.path}
                    exact={route.exact}
                    component={Component}
                  />
                )}
              )}
            </div>
          </main>
        </div>
      </Router>
    );
  }
}

export default App;
